<template>
    <p-card style="width: 80%; margin: auto">
        <template #content>
            <div class="buttons">
                <div>
                    <p-button
                        label="Users"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/users')"
                        @click="goToUsers"
                    />
                    <p-button
                        label="Drug Inventory"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/drug-inventory')"
                        @click="goToDrugInventory"
                    />
                    <p-button
                        label="Media Inventory"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/media-inventory')"
                        @click="goToMediaInventory"
                    />
                    <p-button
                        label="Lab Tests"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/lab-tests')"
                        @click="goToLabTests"
                    />
                    <p-button
                        label="Lab Mon"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/lab-mon-items')"
                        @click="goToLabMonItems"
                    />
                    <p-button
                        label="Diagnosis List"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/diagnosis-inventory')"                     
                        @click="goToDaignosisInventory"
                    />
                    <p-button
                        label="Security Groups"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/groups')"
                        @click="goToUserGroups"
                    />
                    <p-button
                        label="Born (All)"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('born')"
                        @click="goToBorn"
                    />
                    <p-button
                        label="Log"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/logs')"
                        @click="goToLogs"
                    />
                    <p-button
                        label="Completed List"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/born-complete-list')"
                        @click="goToBornCompleteList"
                    />
                    <p-button
                        label="Uncompleted List"
                        class="p-button-lg p-m-2"
                        v-if = "hasPermission('maintanance/born-uncomplete-list')"
                        @click="goToBornUncompleteList"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
    setup() {
        const router = useRouter();
        const store = useStore();
        const hasPermission = (name) => {
            if (!store.state.user) return false;
            let permission = store.state.user.permissions.find(
                (item) => item.appKey == name
            );
            if (permission == undefined) return false;
            return permission.readPermission;
        };
        return {
            goToUsers: () => {
                router.push({ path: "/maintanance/users/" });
            },
            goToDrugInventory: () => {
                router.push({ path: "/maintanance/drug-inventory/" });
            },
            goToMediaInventory: () => {
                router.push({ path: "/maintanance/media-inventory/" });
            },
            goToLabTests: () => {
                router.push({ path: "/maintanance/lab-tests/" });
            },
            goToLabMonItems: () => {
                router.push({ path: "/maintanance/lab-mon-items/" });
            },
            goToDaignosisInventory: () => {
                router.push({ path: "/maintanance/diagnosis-inventory/" });
            },
            goToBorn: () => {
                router.push({ path: "/born" });
            },
            goToUserGroups: () => {
                router.push({ path: "/maintanance/groups/" });
            },
            goToLogs: () => {
                router.push({ path: "/maintanance/logs" });
            },
            goToBornCompleteList: () => {
                router.push({ path: "/maintanance/born-complete-list" });
            },
            goToBornUncompleteList: () => {
                router.push({ path: "/maintanance/born-uncomplete-list" });
            },
            hasPermission
        };
    }
};
</script>

<style scoped>
.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media (max-width: 768px) { 
    .buttons {
        flex-direction: column;
    }

    .buttons .p-button {
        width: 100%;
    }
}
</style>
